:root{
    --main-color: #3b5998;
    --color-dark: #1D2231;
    --text-grey:  #8390A1;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
}

               /* step--1 */
.sidebar{
    width: 345px;
    position: fixed;
    height: 100%;
    left: 0;
    top: 0;
    background-color: #3b5998;
    background-color: var(--main-color);
    z-index: 100;
    transition: width 300ms;   /* step--8 er shomy boshbobe */
}


             /* step--3 */
.sidebar-brand{
    height: 90px;
    padding: 1rem 0rem 1rem 2rem;
    color: #fff;
}
.sidebar-brand span{
    display: inline-block;
    padding-right: 1rem;
}

        /* step--6 */

.sidebar-menu{
    margin-bottom: 1rem;
}

.sidebar-menu li{
    width: 100%;
    margin-bottom: 1.7rem;
    padding-left: 1rem;
}

.sidebar-menu a {
    padding-left: 1rem;
    display: block;
    color: #fff;
    font-style: 1.1rem;
}

.sidebar-menu a span:first-child {
    font-style: 1.5rem;
    padding-right: 1rem;
}
                        /* step--4 */
.sidebar-menu a.active {
    background-color: #fff;
    padding-top: 1rem;
    padding-bottom: 1rem;
    color: #3b5998;
    color: var(--main-color);
    border-radius: 30px 0px 0px 30px;
 }
              /* step--2 */
.main-content{
    transition: margin-left 300ms;   /* step--8 er shomy boshbobe */
    margin-left: 350px;
}

              /* step--5 */
header {
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    box-shadow: 2px 2px 5px rgba(0,0,0,0.2);  /*  user-wrapper small--1   */  
    position: fixed;
    left: 345px;
    width: calc(100% - 345px);
    top: 0;
    z-index: 100;
    transition: left 300ms;   /* step--8 er shomy boshbobe */
}

header h2{
    color: #222;
}

header label span {
    font-size: 1.7rem;
    padding-right: 1rem;
}

.search-wrapper {
    border: 1px solid #ccc;
    border-radius: 30px;
    height: 50px;
    display: flex;
    align-items: center;
    overflow-x: hidden;
}

.search-wrapper input {
    height: 100%;
    padding: .5rem;
    border: none;
    outline: none;
}

.search-wrapper span {
    display: inline-block;
    padding: 0rem 1rem;
    font-size: 1.5rem;
}

.user-wrapper {
    display: flex;
    align-items: center;
}

.user-wrapper img {
    border-radius: 50%;
    margin-right: 1rem;
    border: 1px solid black;
}
.user-wrapper small{
    display: inline-block;
    color: #8390A1;
    color: var(--text-grey);
}


              /* step--6 */

main{
    margin-top: 85px;
    padding: 2rem 1.5rem;
    background-color: #f1f5f6;
    min-height: calc(100vh - 90px);
}
.cards{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2rem;
    margin-top: 1rem;
}

.card-single {
    display: flex;
    justify-content: space-between;
    background: #fff;
    padding: 2rem;
    border-radius: 2px;
}

.card-single div:last-child span{
    font-size: 3rem;
    color: #3b5998;
    color: var(--main-color);
}

.card-single div:first-child span {
   color: #8390A1;
   color: var(--text-grey);
}

.card-single:last-child {
    background: #3b5998;
    background: var(--main-color);
}

.card-single:last-child h1,
.card-single:last-child  div:first-child span,
.card-single:last-child  div:last-child span {
   color: #fff;
}


              /* step--7 */
table {
    border-collapse: collapse;
}

.recent-grid{
    margin-top: 3.5rem;
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: 65% auto;
}

.card {
    background-color: #fff;
    border-radius: 5px;
}

.card-header,
.card-body {
    padding: 1rem;
}

.card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.card-header button{
    background: #3b5998;
    background: var(--main-color);
    border-radius: 10px;
    color: #fff;
    font-size:  .8rem;
    padding: .5rem 1rem;
    border: 1px solid #3b5998;
    border: 1px solid var(--main-color);
}

thead tr {
    border-top: 1px solid #f0f0f0;
    border-bottom: 2px solid #f0f0f0;
}

thead td {
  font-weight: 700;
}

td{
    padding: .5rem 1rem;
    font-size: .9rem;
    color: #222;
}

td .status{
    display: inline-block;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    margin-right: 1rem;
}

.status.purple {
    background: rebeccapurple;
}

.status.pink {
    background: deeppink;
}

.status.orange{
    background: orangered;
}

tr td:last-child {
    display: flex;
    align-items: center;
}
.table-resposive{
    width: 100%;
    overflow-x: auto;
}


              /* step--8   customer*/

.customer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .5rem .7rem;
}

.info {
    display: flex;
    align-items: center;
}

.info img{
    border-radius: 50%;
    margin-right: 1rem;
    border: 1px solid black;
    margin-left: 0rem;
}

.info h4 {
    font-size: .8rem;
    font-weight: 700;
    color: #222;
}

.info small {
    font-weight: 600;
    color: #8390A1;
    color: var(--text-grey);
}

.contact span {
    font-size: 1.2rem;
    display: inline-block;
    margin-left: .5rem;
    color: #3b5998;
    color: var(--main-color);
}


              /* step--8   nav-toggle*/
              
#nav-toggle:checked + .sidebar {
    width: 70px;
}
#nav-toggle:checked + .sidebar .sidebar-brand,
#nav-toggle:checked + .sidebar li  {
    padding-left: 1rem;
    text-align: center;
}

#nav-toggle:checked + .sidebar li a {
    padding-left: 0rem;
}


#nav-toggle:checked + .sidebar .sidebar-brand h2 span:last-child,
#nav-toggle:checked + .sidebar li a span:last-child{
   display: none;
}

#nav-toggle:checked ~ .main-content {
    margin-left: 70px;
}


#nav-toggle:checked ~ .main-content header{
   width: calc(100% - 70px);
   left: 70px;
}


     /* step--9  media*/

@media only screen and (max-width: 1200px){
               
    .sidebar {
        width: 70px;
    }

    .sidebar .sidebar-brand,
    .sidebar li  {
        padding-left: 1rem;
        text-align: center;
    }


    .sidebar li a {
        padding-left: 0rem;
    }


    .sidebar .sidebar-brand h2 span:last-child,
    .sidebar li a span:last-child{
         display: none;
    }

    .main-content {
         margin-left: 70px;
    }


    .main-content header{
        width: calc(100% - 70px);
        left: 70px;
    }




    .sidebar:hover {
        width: 345px;
        z-index: 200;
    }

    .sidebar:hover .sidebar-brand,
    .sidebar:hover li  {
        padding-left: 2rem;
        text-align: left;
    }


    .sidebar li:hover a {
        padding-left: 1rem;
    }


    .sidebar:hover .sidebar-brand h2 span:last-child,
    .sidebar:hover li a span:last-child{
         display: inline;
    }
 
}

@media only screen and  (max-width: 960px){
    .cards {
        grid-template-columns: repeat(3, 1fr);
    }

    .recent-grid {
        grid-template-columns: 60% 40%;
    }
}


@media only screen and  (max-width: 768px){
    .cards {
        grid-template-columns: repeat(2, 1fr);
    }

    .recent-grid {
        grid-template-columns: 100%;
    }
    .search-wrapper{
        display: none;
    }

    .sidebar {
        left: -100% !important;
    }
    header h2 {
        display: flex;
        align-items: center;
    }

    header h2 label {      
        display: inline-block;
        background: #3b5998;
        background: var(--main-color);
        padding-right: 0rem;
        margin-right: 1rem;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center !important;
    }
    header h2 span {
        text-align: center;
        padding-right: 0rem;
    }
    header h2 {
        font-size: 1rem;
    }
    .main-content {
        width: 100%;
        margin-left: 0rem;
    }

    header {
        width: 100% !important;
        left: 0 !important;
    } 

    #nav-toggle:checked + .sidebar {
        left: 0 !important;
        z-index: 100;
        width: 345px;
    }

    #nav-toggle:checked + .sidebar .sidebar-brand,
    #nav-toggle:checked + .sidebar:hover li  {
        padding-left: 2rem;
        text-align: left;
    }


    #nav-toggle:checked + .sidebar li a {
        padding-left: 1rem;
    }


    #nav-toggle:checked + .sidebar .sidebar-brand h2 span:last-child,
    #nav-toggle:checked + .sidebar li a span:last-child{
         display: inline;
    }

    #nav-toggle:checked  ~ .main-content {
        margin-left: 0rem !important;
    }
}

@media only screen and  (max-width: 560px) {
    .cards {
        grid-template-columns: 100%;
    }
}



